import React from 'react';
import Select from '@material-ui/core/Select';
import Pagination from "react-js-pagination";
//import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import env from "../config/env";
import axios from "axios";
import { connect } from 'react-redux';

//import { store } from '../../store/store';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}

class SelectProyecto extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      Proyectos: { data: [] },
      proyecto_id: '',
      pageNumber: 1,
    }
  }

  componentDidMount() {
    this.getProyectos(this.state.pageNumber);
  }


  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.props.setproyecto(jsonState);
  }

  async getProyectos(page) {
    try {
      let res = await axios({
        url: env.endpoint + '/proyectos?page=' + page,
        method: 'get',
        //  //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ Proyectos: res.data });
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  listProyecto() {
    return this.state.Proyectos.data.map(row => {
      return (
        <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
      );
    });
  }

  handlePageChange = (pageNumber) => {
    this.getProyectos(pageNumber);
    this.setState({ activePage: pageNumber })
  }

  validarValue(propsValue) {
    if (propsValue !== '') {
      if (this.state.Proyectos.data.filter(project => project.id === propsValue).length === 0) {
        
        this.state.Proyectos.data.push(this.props.DatoSelect)
      }
    }
    return propsValue;
  }

  render() {
    return (
      <Select
        error={this.props.error.proyecto ? true : false}
        fullWidth={true}
        value={this.validarValue(this.props.value)}
        onChange={(e) => this.syncField(e, 'proyecto_id')}
        inputProps={{
          name: 'proyecto',
          id: 'proyecto',
        }}>

        <MenuItem value="">
          <em>None</em>
        </MenuItem>
        {this.listProyecto()}
        {this.state.Proyectos.data.length > 0 ?
          <Pagination
            activePage={this.state.activePage}
            itemsCountPerPage={10}
            totalItemsCount={this.state.Proyectos.total}
            pageRangeDisplayed={5}
            onChange={this.handlePageChange}
          /> : ''}
      </Select>
    )
  }
}

export default connect(mapSteToProps)(SelectProyecto);