import React from 'react'
import PropTypes from 'prop-types'
import { withRouter } from 'react-router'
import SearchIcon from '@material-ui/icons/Search';
const Styles= {
    btn:{
        color: '#fff',
    }
}
const Search = (props) => {
  const {
    history,
    location,
    match,
    staticContext,
    Search,
    onClick,
    // ‚¨Ü filtering out props that `button` doesn‚Äôt know what to do with.
    ...rest
  } = props
  return (
    <SearchIcon
    style={Styles.btn}
      {...rest} // `children` is just another prop!
      onClick={(event) => {
    
      history.replace('/propuesta/'+ Search );
    //    history.push('/propuesta/'+ Search)
      }}
    />
  )
}
Search.propTypes = {
  children: PropTypes.node.isRequired
}

export default withRouter(Search)