import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import PropTypes from 'prop-types';
import blue from '@material-ui/core/colors/blue';

const styles = {
  avatar: {
    backgroundColor: blue[100],
    color: blue[600],
  },
};

class Listfilter extends React.Component {
  handleClose = () => {
    this.props.onClose();
  };
  handleListItemClick = value => {
    this.props.handleSelect(value);
  };

mapdata() {
  if (this.props.datos) {
    return this.props.datos.map(row => {
        return (
     <ListItem button onClick={() => this.handleListItemClick({data:row, action: this.props.action})} key={row.id}>
                <ListItemAvatar>
                  <Avatar >
                    <i className="material-icons"> find_in_page </i>
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary={row.nombre } />
              </ListItem>
        );
    });
  }else{
    return "";
  }
}
  render() {
    const { classes, onClose, handleSelect ,selectedValue, ...other } = this.props;
    return (
      <Dialog onClose={this.handleClose} aria-labelledby="simple-dialog-title" {...other}>
      
        <DialogTitle id="simple-dialog-title">{this.props.title}</DialogTitle>
        <div>
          <List>
           {this.mapdata()}
            <ListItem>
              
              <Button variant="outlined" color="primary" onClick={() => this.handleClose()}>
              Cancelar
             </Button>
            </ListItem>
          </List>
        </div>
      </Dialog>
    );
  }
}

Listfilter.propTypes = {
  onClose: PropTypes.func,
  selectedValue: PropTypes.string,
};

export default withStyles(styles) (Listfilter) ;