import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Pagination from "react-js-pagination";
import Slide from '@material-ui/core/Slide';

function Transition(props) {
    return <Slide direction="up" {...props} />;
}
class ListDetalleGasto extends React.Component {
    state = {
        fullWidth: true,
        maxWidth: 'md',
    };

    getMapData() {
        if (this.props.datos.data.length > 0) {
            return this.props.datos.data.map((row, index) => {
                return (
                    <TableRow key={index}>
                        <TableCell > {row.id} </TableCell>
                        <TableCell > {row.NombreCompra}</TableCell>
                        <TableCell > {row.objetivo}</TableCell>
                        <TableCell > {row.servicio}</TableCell>
                        <TableCell >{
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.totalPago)
                        } - ({row.porcentajedegasto}%)</TableCell>
                        <TableCell > {row.name} {row.apellido}</TableCell>
                    </TableRow>
                );
            });
        }
        return "";
    }

    setData() {
        return (
            <div >
                <Table  >
                    <TableHead>
                        <TableRow>
                            <TableCell>ID Compra</TableCell>
                            <TableCell>Nombre Compra</TableCell>
                            <TableCell >Objetivo</TableCell>
                            <TableCell >Servicio </TableCell>
                            <TableCell >Valor Total</TableCell>
                            <TableCell >Nombres Responsable</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                    </TableBody>
                </Table>
                {this.props.datos.data.length > 0 ?
                    <Pagination
                        activePage={this.props.activePage}
                        itemsCountPerPage={10}
                        totalItemsCount={this.props.datos.total}
                        pageRangeDisplayed={5}
                        onChange={this.props.handlePageChange}
                    /> : ''}
            </div>
        );
    }
    render() {
        return (<div>
            <Dialog
                open={this.props.open}
                onClose={this.props.handleClose}
                TransitionComponent={Transition}
                keepMounted
                fullWidth={this.state.fullWidth}
                maxWidth={this.state.maxWidth}
                aria-labelledby="form-dialog-title"
            >
                <DialogTitle id="form-dialog-title"> Gastos. </DialogTitle>
                <DialogContent>
                    <Grid item xs={12} sm={12}>
                        <p><b>Cl√∫ster :   </b>
                            {this.props.row.clusterName}
                        </p>
                        <p><b>Componente :</b> {this.props.row.componenteName}
                        </p>
                        <p><b>Concepto:</b>  {this.props.row.proyectoName}
                        </p>
                        <p><b>Centro de Operaci√≥n :</b> {this.props.row.operationName}
                        </p>
                    </Grid>
                    <DialogContentText>
                        A continuaci√≥n se listar√°n las compras que se han gastado de la llave.
          </DialogContentText>
                    {this.setData()}
                </DialogContent>

            </Dialog>
        </div>);
    }

}
export default ListDetalleGasto;