import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon';
import { connect } from 'react-redux';
import MSGEstado from '../../shared/msg-estado';
import env from "../../../components/config/env";
import axios from "axios";
import { store } from '../../../store/store';
import Pagination from "react-js-pagination";
import Snackbar from '@material-ui/core/Snackbar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import SearchIcon from '@material-ui/icons/Search';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';

const styles = {
  tableWrapper: {
    overflowX: 'auto',
  },
};


function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}


export class PageCompraLists extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0,
      user: {},
      dataCompra: { data: [] },
      activePage: 1,
      totalItemsCount: 0,
      open: false,
      vertical: 'top',
      horizontal: 'center',
      search: '', 
      tipo: '',
      rols: { rols: [] },
      error: false,
    }
  }

  handlePageChange = (pageNumber) => {
    this.setState({ activePage: pageNumber }
    );
    this.getComprasFull(pageNumber, this.state.tipo);
  }


  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }


  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
}


  componentDidMount() {
    this.getComprasFull(1, this.state.tipo);
    this.getrols();
    if (this.props.location.search === '?Error404') {
      this.setState({ open: true, vertical: 'bottom', horizontal: 'left' });
    }
  }


  async getrols() { // Obtener los persmisos que tengo 
    try {
      let res = await axios({
        url: env.endpoint + '/myrols/me',
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      this.setState({
        rols: res.data
      })

    } catch (error) {
      this.getError(error.response);
    }
  }


  async getComprasFull(page, tipo) { // Obtener los datos de full compras
    try {
      let res = await axios({
        url: env.endpoint + '/compras/?tipo=' + tipo + '&search=' + this.state.search + '&page=' + page,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      this.setState({
        dataCompra: res.data,
        totalItemsCount: res.data.total
      })

    } catch (error) {
      this.getError(error.response);
    }
  }

  compraLink(id) {
    this.props.history.push('/propuesta/' + id);
  }

  getMapData() {
    if (this.state.dataCompra.data.length > 0) {
      return this.state.dataCompra.data.map((row , index) => {
        return (
          <TableRow key={index}>
            <TableCell > {row.id} </TableCell>
            <TableCell > {row.NombreCompra}</TableCell>
            <TableCell >{row.objetivo}</TableCell>
            <TableCell >{
              new Intl.NumberFormat('COP', {
                style: 'currency',
                currency: 'COP',
                minimumFractionDigits: 0,
                maximumFractionDigits: 0
              }).format(row.presupuestoCompra)
            }
            </TableCell>
            <TableCell >{row.updated_at}</TableCell>
            <TableCell ><MSGEstado estado={row.EstadoAction} /></TableCell>
            <TableCell >
              <Icon variant="contained" color="primary" onClick={() => this.compraLink(row.id)} >visibility</Icon></TableCell>
          </TableRow>
        );
      });
    }
    return "";
  }

  setData() {
    return (<Paper>
      <div style={styles.tableWrapper}>
        <Table  >
          <TableHead>
            <TableRow>
              <TableCell>N¬∫ Compra</TableCell>
              <TableCell>Nombre Compra</TableCell>
              <TableCell >Objetivo</TableCell>
              <TableCell >Total Presupuestado</TableCell>
              <TableCell >Fecha de Creaci√≥n</TableCell>
              <TableCell >Estado</TableCell>
              <TableCell >Acciones</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {this.getMapData()}
          </TableBody>
        </Table>

        {this.state.dataCompra.data.length > 0 ?
          <Pagination
            activePage={this.state.activePage}
            itemsCountPerPage={10}
            totalItemsCount={this.state.totalItemsCount}
            pageRangeDisplayed={5}
            onChange={this.handlePageChange}
          /> : ''}
      </div>
    </Paper>);
  }
  handleClick = state => () => {
    this.setState({ open: true, ...state });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  cambiarview = (event, value) => {
    if (value > 0) {
      this.setState({
        value: value,
        tipo: this.state.rols.rols[value - 1].id,
        activePage: 1
      });
      this.getComprasFull(1, this.state.rols.rols[value - 1].id);
    } else {
      this.setState({ value: value, tipo: '', activePage: 1 })
      this.getComprasFull(1, '');
    }
  };
  
  maprols() {
    return this.state.rols.rols.map(row => {
      return (
        <Tab key={row.id} label={row.name} />
      )
    });
  }
  rols() {
    if (this.state.rols.rols.length > 0) {
      return (
        <Paper square>
          <Tabs
            value={this.state.value}
            indicatorColor="primary"
            textColor="primary"
            onChange={this.cambiarview}
          >
            <Tab label="Todas las compras" />
            {this.maprols()}
          </Tabs>
        </Paper>
      );
    } else {
      return "";
    }
  }


  Buscar() {
    return (
      <div>
        <h3>Compras</h3>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6}>

            <TextField
              error={this.state.error ? true : false}
              label={this.state.error ? this.state.error : "search"}
              placeholder="search"
              value={this.state.search}
              onChange={(e) => this.syncField(e, 'search')}
              fullWidth={true}
            />
              <p>Recuerda que si hay algo en el buscador afecta el resultado... </p>
          </Grid>
          <Grid item xs={12} sm={6}>
          <SearchIcon onClick={() => this.getComprasFull(1, this.state.tipo)} />
          </Grid>
        
        </Grid>
      </div>
    )
  }

  render() {
    const { vertical, horizontal, open } = this.state;
    return (<div>
      <div>
        <Snackbar
          anchorOrigin={{ vertical, horizontal }}
          open={open}
          onClose={this.handleClose}
          ContentProps={{
            'aria-describedby': 'message-id',
          }}
          message={<span id="message-id">Error Compra no existe...</span>}
        />
      </div>

      {this.rols()}
      {this.Buscar()}
      {this.setData()}
    </div>
    );
  }
}

export default connect(mapSteToProps)(PageCompraLists);