import React from "react";
import {
  Route,
 // Redirect, 
  Switch, 
} from 'react-router-dom';
//import shared
//importar componentes view 
import Login from './components/login/container/login';
import PageCompraForm from './components/compra/container/page-compra-form';
import PageCompraLists from './components/compra/container/page-compra-list';
import Home from './components/home/home';

import ContainerUser from './components/user/container-user';
import PrivateRoute from './helpers/privateRoute';
import PrivateRouteSuperAdmin from './helpers/privateRouterSuperAdmin';
import PageProvedor from './components/Provedor/page-provedor';


//form administrador
import FormCluster from './components/clusters/form-cluster';
import FormConcepto from './components/conceptos/form-concepto';
import FormComponente from './components/componentes/form-componente';

import PagePropuesta from './components/propuesta/container/page-propuesta';
import PagePresupuesto from './components/presupuesto/page-presupuesto';
import PdfPoliticas from './components/politicas/pdf-politicas';
import FormRules from './components/rules/form-rules';

 
function AppRouter() {
    return  (
            <div>
            <Switch>
            <Route path="/login" component={Login} />
            <Route exact path="/" component={Login} />

            <PrivateRoute path="/home" component={Home} />
            <PrivateRoute path="/compra" component={PageCompraForm} />
            <PrivateRoute path="/compras" component={PageCompraLists} />
            <PrivateRoute path="/propuesta/:idCompra" component={PagePropuesta} />
            <PrivateRoute path="/presupuesto" component={PagePresupuesto} />
            <PrivateRoute path="/Politicas" component={PdfPoliticas} />
            
            
            <PrivateRouteSuperAdmin path="/AdminUser" component={ContainerUser} />
            <PrivateRouteSuperAdmin path="/AdminProvedores" component={PageProvedor} />
            <PrivateRouteSuperAdmin path="/clusters" component={FormCluster} />
            <PrivateRouteSuperAdmin path="/componentes" component={FormComponente} />
            <PrivateRouteSuperAdmin path="/conceptos" component={FormConcepto} />
            <PrivateRouteSuperAdmin path="/Rules" component={FormRules} />
            <Route component={NoMatch} />
            </Switch>
            </div> 
    );
}

function NoMatch({ location }) {
  return (
    <div>
      <h3>
        Error pagina no encontrada  <code>{location.pathname}</code>
      </h3>
    </div>
  );
}

export default AppRouter;




