import React from 'react';
import TextField from '@material-ui/core/TextField';
import env from "../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

import { connect } from 'react-redux';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import { store } from '../../store/store';
import NumberFormatCustom from '../shared/number-format-custom';
import jwtDecode from "jwt-decode";

import ListPresupuesto from './list-presupuesto';
import Listfilter from './filter/list-filter';
import SelectProyecto from '../proyectos/select';
function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}

class FormPresupuesto extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      cluster_idF: 0,
      cluster_id: '',
      componente_idF: 0,
      componente_id: '',
      operacion_idF: 0,
      operacion_id: '',
      valor: '',
      proyecto_id: '',
      nombreProyecto: '',
      componentes: [],
      operacions: [],
      Cluster: [],
      open: false,
      datos: { data: [] },
      Proyectos: { data: [] },
      datafilter: [],
      error: {},
      title: 'Buscando',
      canSubmit: true,
      action: true,
      Amsg: false,
      clusterA: false,
      componenteA: false,
      operationn: false,
      actionf: '',
      msg: '',
    };
  }
  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }

  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }

  componentDidMount() {
    this.getcluster();
    this.getComponente();
    this.getOperacion();
    this.getData(1, 1);
  }
  handlePageChange = (pageNumber) => {
    //console.log(`active page is ${pageNumber}`);
    //console.log(this.state.actionf);
    if (this.state.actionf === '') {
      this.setState({ activePage: pageNumber }
      );
      this.getData(1, pageNumber);
    } else {
      this.setState({ activePage: pageNumber }
      );
      this.getDatafilter(1, pageNumber, this.state.cluster_idF, this.state.componente_idF, this.state.operacion_idF);
    }
  }





  modificar = (id) => {
    this.getpresupuesto(id)
  }


  async getData(id, page) {
    try {
      let res = await axios({
        url: env.endpoint + '/Presupuesto/' + id + '?page=' + page,
        method: 'get',
        //   //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ datos: res.data });

      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }


  async getpresupuesto(id) { // Obtener los datos para modificar el detalle del presupuestado
    try {
      let res = await axios({
        url: env.endpoint + '/presupuestos/' + id,
        method: 'get',
        // //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({
          action: false,
          id: res.data.id,
          cluster_id: res.data.cluster_id,
          componente_id: res.data.componente_id,
          operacion_id: res.data.operacion_id,
          proyecto_id: res.data.proyecto_id,
          valor: res.data.valor,
          nombreProyecto: res.data.nombreProyecto
        })
      }
    } catch (error) {
      this.getError(error.response);
    }
  }

  clear() {
    this.setState({
      id: '',
      cluster_id: '',
      componente_id: '',
      operacion_id: '',
      proyecto_id: '',
      error: {},
      valor: '',
      action: true,
    })
  }

  onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/presupuestos',
      data: {
        periodo_id: 1,
        cluster_id: this.state.cluster_id,
        componente_id: this.state.componente_id,
        operacion_id: this.state.operacion_id,
        proyecto_id: this.state.proyecto_id,
        valor: this.state.valor,
        action: false
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      this.clear();
      this.getData(1, 1);
      this.setState({ canSubmit: true });
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }
  onSubmitEditar = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'put',
      url: env.endpoint + '/presupuestos/' + this.state.id,
      data: {
        periodo_id: 1,
        cluster_id: this.state.cluster_id,
        componente_id: this.state.componente_id,
        operacion_id: this.state.operacion_id,
        proyecto_id: this.state.proyecto_id,
        valor: this.state.valor

      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      this.clear();
      this.setState({ canSubmit: true });
      this.getData(1, 1);
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }

  async getcluster() {
    try {
      let res = await axios({
        url: env.endpoint + '/clusters/',
        method: 'get',
        //  //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({
          Cluster: res.data,
          clusterA: true,
        });
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  async getComponente() {
    try {
      let res = await axios({
        url: env.endpoint + '/componentes/',
        method: 'get',
        //  //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({
          componentes: res.data,
          componenteA: true,
        });
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }


  async getOperacion() {
    try {
      let res = await axios({
        url: env.endpoint + '/operacions/',
        method: 'get',
        //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({
          operacions: res.data,
          operationn: true,
        });
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }
  listCluster() {
    return this.state.Cluster.map(row => {
      return (
        <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
      );
    });
  }

  listComponente() {
    return this.state.componentes.map(row => {
      return (
        <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
      );
    });
  }
  listOperation() {
    return this.state.operacions.map(row => {
      return (
        <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
      );
    });
  }


  // config filter
  //abrimos la ventana emergente 
  handleClickOpen = (nameFilter) => {
    switch (nameFilter) {
      case 'cluster':
        this.setState({
          actionf: nameFilter,
          title: 'Seleccione cluster',
          activePage: 1,
          datafilter: this.state.Cluster
        });
        break;
      case 'componente':
        this.setState({
          actionf: nameFilter,
          title: 'Seleccione componente',
          activePage: 1,
          datafilter: this.state.componentes
        });
        break;
      case 'operacion':
        this.setState({
          actionf: nameFilter,
          title: 'Seleccione operacion',
          activePage: 1,
          datafilter: this.state.operacions
        });
        break;
      default:
        break;
    }

    this.setState({
      open: true,

    });
  };
  handleSelect = value => {

    switch (value.action) {
      case 'cluster':
        this.getDatafilter(1, 1, value.data.id, this.state.componente_idF, this.state.operacion_idF);
        this.setState({ cluster_idF: value.data.id });
        break;
      case 'componente':
        this.getDatafilter(1, 1, this.state.cluster_idF, value.data.id, this.state.operacion_idF);
        this.setState({ componente_idF: value.data.id });
        break;
      case 'operacion':
        this.getDatafilter(1, 1, this.state.cluster_idF, this.state.componente_idF, value.data.id);
        this.setState({ operacion_idF: value.data.id });

        break;
      default:
        break;
    };
    this.setState({ open: false });
  }

  cleardata = (e) => {
    this.setState({
      operacion_idF: 0,
      componente_idF: 0,
      cluster_idF: 0,
      Amsg: false,
      actionf: '',
      activePage: 1,
    });
    this.getData(1, 1);
  }


  setproyecto = (data) => {
    this.setState(data)
  }


  handleClose = value => {
    this.setState({ open: false });
  };

  async getDatafilter(id, page, cluster_idF, componente_idF, operacion_idF) {
    try {
      let res = await axios({
        url: env.endpoint + '/Presupuesto/' + id + '/?cluster_id=' + cluster_idF + '&componente_id=' + componente_idF + '&operacion_id=' + operacion_idF + '&page=' + page,
        method: 'get',
        //   //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ datos: res.data });
        if (res.data.data.length === 0) {
          this.setState({
            Amsg: true,
            msg: ' Error no tenemos datos relacionados con este filtro.'
          })
        } else {
          this.setState({
            Amsg: false,
            msg: ''
          })
        }
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }



  render() {
    return (<div>
       {
                  //user Super-Admin
                  jwtDecode(this.props.userAcceso).rol=== 'Super-Admin'? (<div>
      <Grid container spacing={24}>
        <Grid item xs={12} sm={6}>

          <InputLabel htmlFor="Cluster">{this.state.error.cluster ? this.state.error.cluster[0] : "Cl√∫ster"}</InputLabel>
          <Select
            error={this.state.error.cluster ? true : false}
            fullWidth={true}
            value={this.state.cluster_id}
            onChange={(e) => this.syncField(e, 'cluster_id')}
            inputProps={{
              name: 'Cluster',
              id: 'Cluster',
            }}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            {this.listCluster()}

          </Select>
          <InputLabel htmlFor="Componente">{this.state.error.componente ? this.state.error.componente[0] : "Componente"}</InputLabel>
          <Select
            error={this.state.error.componente ? true : false}
            fullWidth={true}
            value={this.state.componente_id}
            onChange={(e) => this.syncField(e, 'componente_id')}
            inputProps={{
              name: 'Componente',
              id: 'Componente',
            }}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            {this.listComponente()}
          </Select>

          <InputLabel htmlFor="proyecto">{this.state.error.proyecto ? this.state.error.proyecto[0] : "Concepto"}</InputLabel>
          < SelectProyecto
            error={this.state.error}
            setproyecto={this.setproyecto}
            value={this.state.proyecto_id}
            DatoSelect={{ id: this.state.proyecto_id, nombre: this.state.nombreProyecto }}
          />
        </Grid>
        <Grid item xs={12} sm={6}>

          <InputLabel htmlFor="operacion">{this.state.error.operacion ? this.state.error.operacion[0] : "Centro de Operaci√≥n"}</InputLabel>
          <Select
            error={this.state.error.operacion ? true : false}
            fullWidth={true}
            value={this.state.operacion_id}
            onChange={(e) => this.syncField(e, 'operacion_id')}
            inputProps={{
              name: 'operacion',
              id: 'operacion',
            }}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            {this.listOperation()}
          </Select>
          <TextField
            error={this.state.error.valor ? true : false}
            label={this.state.error.valor ? this.state.error.valor[0] : "Total Presupuestado"}
            placeholder="Valor total presupuestado"
            value={this.state.valor}
            onChange={(e) => this.syncField(e, 'valor')}
            id="formatted-numberformat-input"
            fullWidth={true}
            InputProps={{
              inputComponent: NumberFormatCustom,
            }}
          />
        </Grid>
      </Grid>
     { this.state.action ?
        <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
        <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
     }
     </div>) :''}
      
      
      


      <Listfilter
        action={this.state.actionf}
        title={this.state.title}
        selectedValue={this.state.selectedValue}
        open={this.state.open}
        onClose={this.handleClose}
        handleSelect={this.handleSelect}
        datos={this.state.datafilter}
      />

      <ListPresupuesto
        clusterA={this.state.clusterA}
        componenteA={this.state.componenteA}
        operationn={this.state.operationn}
        cleardata={this.cleardata} handleClickOpen={this.handleClickOpen}
        datos={this.state.datos} modificar={this.modificar}
        handlePageChange={this.handlePageChange}
        activePage={this.state.activePage}
        action={jwtDecode(this.props.userAcceso).rol=== 'Super-Admin'}
      />
      {this.state.Amsg ? <p >{this.state.msg}</p> : ''}
    </div>);
  }
}

export default connect(mapSteToProps)(FormPresupuesto);