import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Snackbar from '@material-ui/core/Snackbar';
import MySnackbarContent from '../../shared/message';
//import shared
import Message from '../../shared/message';
//componentes de la vista
import InfoUser from '../componentes/info-user';
import FormCompra from '../componentes/form-compra';
import DetallePresupuesto from '../componentes/detallePresupuesto/detallePresupuesto';
import FormTerminosReferencia from '../componentes/form-terminosReferencia';
import FormEntregables from '../componentes/form-entregables';
import axios from "axios";
import env from "../../config/env";
import DetallesCompra from '../componentes/detalles/detallesCompra';
import { connect } from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}


function TabContainer({ children }) {
  return (
    <div style={{ padding: 8 * 3 }}>
      {children}
    </div>
  );
}
TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};
const Styles = ({
  cont: {
    display: 'flex',
    justifyContent: 'center'
  },

});
const styles = theme => ({

  root: {
    width: '98%',
  },
  cont: {
    display: 'flex',
    justifyContent: 'center'
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
  },
  toolbar: theme.mixins.toolbar,

});


class PageCompraForm extends React.Component {

  getSteps() {
    return [this.state.user.id ? 'Solicitante: ' + this.state.user.name + ' ' + this.state.user.apellido : 'Datos Solicitante ',
    this.state.compra.NombreCompra ? 'Compra : ' + this.state.compra.id + '   ' +this.state.compra.NombreCompra :'Datos Generales de Compra',
      'Presupuesto',
      'T√©rminos de Referencia',
      'Entregables'];
  }

  componentDidMount() {
    this.ValidarPresupuesto();
  }
  getStepContent(stepIndex) {
    switch (stepIndex) {
      case 0:
        return <InfoUser btns={this.btns} USER={this.useradd} userData={this.state.user} compra={this.state.compra} compraData={this.compra} />;  //falta que pudan escoger otra jajaa
      case 1:
        return this.state.user.id ? <FormCompra handleBack={this.handleBack} handleNext={this.handleNext} compra={this.state.compra} compraData={this.compra} msgbn={this.msgbn} btns={this.btns} user={this.state.user} /> : 'Error se requiere un persona que sea responsable de esta compra';
      case 2:
        return <DetallePresupuesto periodo={this.state.peridoPresupuesto} compra={this.state.compra} msgbn={this.msgbn} CanNext={this.CanNext} btns={this.btns} />
      case 3:
        return <FormTerminosReferencia compra={this.state.compra} msgbn={this.msgbn} CanNext={this.CanNext} btns={this.btns} />;
      case 4:
        return <FormEntregables compra={this.state.compra} msgbn={this.msgbn} CanNext={this.CanNext} btns={this.btns} />;
      default:
        return <Message />;
    }
  }



  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
      compra: {},
      user: { id: 0 },
      open: false,
      msgMo: '',
      peridoPresupuesto: {},
      Tipo: 'success',
      error: {},
      presupuestoAfect: { data: [] },
      entregables: [],
      TerminosReferencia: [],
      presupuesto: { data: [] },
      fechaSolicitud: '',
      FechaInicio: '',
      FechaFin: '',
      canNext: true,
      stateBtn: true,
    }
    this.handleNext = this.handleNext.bind(this);
    this.handleBack = this.handleBack.bind(this);


  }
  handleNext = () => {
    this.setState(state => ({
      activeStep: state.activeStep + 1,
    }));


    if (this.state.activeStep + 1 === 4) {
      this.getPresupuesto(this.state.compra.id);
      this.getTerminos(this.state.compra.id);
      this.getentregables(this.state.compra.id);
      var dt = new Date(this.state.compra.created_at).toLocaleDateString();
      this.setState({
        fechaSolicitud: dt,
        FechaInicio: new Date(this.state.compra.FechaInicio).toLocaleDateString(),
        FechaFin: new Date(this.state.compra.FechaFin).toLocaleDateString(),
      })

    }
  };

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
    }));
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };
  compra = (data) => {
    this.setState(state => ({
      compra: data,
    }));
  }
  useradd = (data) => {
    this.setState(state => ({
      user: data,
    }));
    this.handleNext();
  }

  CanNext = (estado) => {
    this.setState({
      canNext: estado,
    });

  }
  btns = (estado) => {
    this.setState({
      stateBtn: estado,
    })
  }
  msgbn = (mensage) => {
    this.setState({
      open: true,
      msgMo: mensage
    });
  }
  Publicar = () => {
    this.getPublicar();
  }
  async ValidarPresupuesto() {
    try {
      let res = await axios({
        url: env.endpoint + '/Presupuesto',
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      this.setState({
        peridoPresupuesto: res.data
      })
    }
    catch (error) {
      if (error.response.status === 401) {
        this.setState({
          error: { error: true, menssage: error.response.data.errors }
        })
      }
    }
  }
  async getPublicar() {
    try {
      await axios({
        url: env.endpoint + '/publicar/' + this.state.compra.id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      this.Redirigir();
    }
    catch (error) {

    }
  }
  Redirigir() {
    this.props.history.push('/propuesta/' + this.state.compra.id + '?R');
  }



  async getPresupuesto(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/ListdetallePresupuesto/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ presupuesto: res.data });

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  async getTerminos(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listreferenceterms/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({ TerminosReferencia: res.data });

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      //  this.getError(error.response);
    }
  }

  async getentregables(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listEntregables/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({ entregables: res.data });
      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }


  render() {
    const { classes } = this.props;
    const steps = this.getSteps();
    const { activeStep } = this.state;
    return (this.state.peridoPresupuesto.id ?
      <TabContainer>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.open}
          autoHideDuration={6000}
          onClose={this.handleClose}
        >
          <MySnackbarContent
            onClose={this.handleClose}
            variant={this.state.Tipo}
            message={this.state.msgMo}
          />
        </Snackbar>
        <p>Presupuesto: {this.state.peridoPresupuesto.nombre}</p>
        <Stepper activeStep={activeStep} alternativeLabel>
          {steps.map(label => {
            return (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            );
          })}
        </Stepper>
        <div className={classes.toolbar}>
          {this.state.activeStep === steps.length ? (
            <div >
              <Typography className={classes.instructions}> Por favor revisar si todo se encuentra bien; de lo contrario corregir.</Typography>
              {this.state.compra.id > 0 ?
                <DetallesCompra
                  user={this.state.user}
                  entregables={this.state.entregables}
                  TerminosReferencia={this.state.TerminosReferencia}
                  presupuesto={this.state.presupuesto}
                  fechaSolicitud={this.state.fechaSolicitud}
                  FechaInicio={this.state.FechaInicio}
                  FechaFin={this.state.FechaFin}
                  COMPRADATA={this.state.compra} /> :
                <div style={Styles.cont} >   <CircularProgress color="secondary" /> </div>
              }

              <div className={classes.cont} >
                <Button onClick={this.handleReset}>Volver</Button>
                <Button variant="contained" color="primary" onClick={this.Publicar}  >Confirmar</Button>
              </div>
            </div>
          ) :
            this.state.stateBtn === true ? (
              <div   >
                <div >{this.getStepContent(activeStep)}</div>
                <div className={classes.cont} >
                  <Button
                    disabled={activeStep === 0}
                    onClick={this.handleBack}
                    className={classes.backButton}
                  >
                    anterior
                 </Button>
                  <Button disabled={!this.state.canNext} variant="contained"
                    color="primary" onClick={this.handleNext}>
                    {activeStep === steps.length - 1 ? 'Revisar' : 'Siguiente'}
                  </Button>
                </div>
              </div>
            ) : (
                <div >{this.getStepContent(activeStep)}</div>
              )}
        </div>
      </TabContainer> : this.state.error.error ? <div style={Styles.cont} >{this.state.error.menssage}</div> :
        <div style={Styles.cont} > <CircularProgress className={classes.progress} color="secondary" /></div>
    );
  }
}
PageCompraForm.propTypes = {
  classes: PropTypes.object,
};

export default connect(mapSteToProps)(withStyles(styles)(PageCompraForm));