import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon';
import Pagination from "react-js-pagination";

const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};

export class ListConceptos extends React.Component {

    modificar(id) {
        this.props.modificar(id);
    }

    eliminar(id) {
        this.props.onEliminar(id);
    }

    getMapData() {
        if (this.props.datos) {
            return this.props.datos.data.map(row => {
                return (
                    <TableRow key={row.id}>
                        <TableCell > {row.nombre} </TableCell>
                        <TableCell >{row.activo?'activo': 'No activo'}</TableCell>
                        {this.props.action ? 
                        
                        <TableCell >
                            <Icon color="primary" onClick={() => this.modificar(row.id)} >edit_icon</Icon>
                            
                        </TableCell> : <TableCell ></TableCell>}
                    </TableRow>
                );
            });
        }
        return "";
    }
    setData() {
        return (<Paper >
            <div style={styles.tableWrapper}>
                <Table  >
                    <TableHead>
                        <TableRow>
                            <TableCell>Nombre </TableCell>
                            <TableCell >Estado</TableCell>
                            {this.props.action ? <TableCell >Acciones</TableCell> : <TableCell ></TableCell>}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                    </TableBody>
                </Table>
                {this.props.datos.data.length > 0 ?
                    <Pagination
                        activePage={this.props.activePage}
                        itemsCountPerPage={10}
                        totalItemsCount={this.props.datos.total}
                        pageRangeDisplayed={5}
                        onChange={this.props.handlePageChange}
                    /> : ''}
            </div>
        </Paper>);
    }
    render() {
        return this.setData();
    }
}

export default ListConceptos;