import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';




class Acciones extends React.Component {


 
  

  render() {
    return (
      <div>
        
        <Button variant="outlined" onClick={this.props.handleClickOpen}>
          Acciones compra
        </Button>
        
        <Dialog
          open={this.props.open}
          onClose={this.props.handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description">

          <DialogTitle id="alert-dialog-title">{"Acciones compra "}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              A continuaci√≥n puedes cambiarle el estado a esta compra.
            </DialogContentText>
            {this.props.ErrorAccion ? (<DialogContentText style={{ color: 'red' }} >
              {this.props.MessageErrorAccion}
            </DialogContentText>) : '' }

            <Grid container spacing={24}>
              <Grid item xs={12} sm={12}>
                {this.props.estado !== 3 ?
                  <RadioGroup
                    value={this.props.estado}
                    onChange={this.props.handleChangeaction}
                  >
                    <FormControlLabel
                      value={0}
                      control={<Radio color="primary" />}
                      label="Pendiente por Confirmar - Edici√≥n"
                      labelPlacement="start"
                    />
                    <FormControlLabel
                      value={1}
                      control={<Radio color="primary" />}
                      label="Compra Anulada"
                      labelPlacement="start"
                    />
                      <FormControlLabel
                        value={2}
                        control={<Radio color="primary" />}
                        label="Compra habilitada para recibir propuestas."
                        labelPlacement="start"
                      />
                      <FormControlLabel
                        value={4}
                        control={<Radio color="primary" />}
                        label="Esperando Aprobaci√≥n- cerrada"
                        labelPlacement="start"
                      /> 
                  </RadioGroup>
                  : 'Esta compra esta aprovada no se puede relizar ninguna acci√≥n.'}
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            {this.props.estado !== 3 ?
              <Button disabled={!this.props.canSubmit} onClick={this.props.updateEstadocompra} color="primary">
                modificar Estado
            </Button>
              : ''}
            <Button disabled={!this.props.canSubmit} onClick={this.props.handleClose} color="primary" autoFocus>
              Cancelar
            </Button>

          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

export default Acciones;