import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import Icon from '@material-ui/core/Icon';
import DeleteIcon from '@material-ui/icons/Delete';

export class Entregables extends React.Component {

    modificar(id) {
        this.props.modificar(id);
    }
    eliminar(id) {
        this.props.onEliminar(id);
    }

    getMapData() {
        if (this.props.datos) {
            return this.props.datos.map(row => {
                return (
                    <TableRow key={row.id}>
                        <TableCell component="th" scope="row">
                            {row.producto}
                        </TableCell>
                        <TableCell numeric>{row.cantidad}</TableCell>
                        <TableCell >{row.observaciones}</TableCell>
                        {this.props.action ? <TableCell >
                            <Icon color="primary" onClick={() => this.modificar(row.id)} >edit_icon</Icon>
                            <DeleteIcon color="error" onClick={() => this.eliminar(row.id)} />
                        </TableCell> : <TableCell ></TableCell>}
                    </TableRow>
                );
            });
        }
        return "";
    }

    setData() {


        return (<Paper>
            <Table >
                <TableHead>
                    <TableRow>
                        <TableCell>Producto o servicio</TableCell>
                        <TableCell numeric>Cantidad</TableCell>
                        <TableCell >Observaciones</TableCell>
                        {this.props.action ? <TableCell >Acciones</TableCell> : <TableCell ></TableCell>}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {this.getMapData()}
                </TableBody>
            </Table>
        </Paper>);
    }
    render() {
        return this.setData();
    }
}
export default Entregables;