import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import MobileStepper from '@material-ui/core/MobileStepper';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import ShowPropuesta from './show-propuesta';
import env from "../../../config/env";
import axios from "axios";
import { connect } from 'react-redux';
import { store } from '../../../../store/store';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}
/**
* listar las propuestas de para la compra, dando un formato en la parte principal
* Nota desde este componente se actualizan los estados de sus hijos por ellos por si solos no se actualizan
*/

const styles = theme => ({

  header: {
    display: 'flex',
    alignItems: 'center',
    height: 50,
    paddingLeft: theme.spacing.unit * 4,
    backgroundColor: theme.palette.background.default,
  },

});
class PagePropuestacontainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
     viewEstate: false, 
      propuestaeconomica: { data: [] },
      propuesta: {},
    };
  }

  componentDidMount() {
    // llamas la lista de propuestas para formar el slaider
    if (this.props.data.length > 0) {
    
      this.getListPropuestaEconomica(this.props.data[0]);
    }
  }


  componentDidUpdate(prevProps) {
    if (prevProps.compra.id !== this.props.compra.id) {
      this.getListPropuestaEconomica(this.props.data[0]);
    }
  }
  
  handleNext = () => {
    this.setState(prevState => ({
      activeStep: prevState.activeStep + 1,
    }));
    this.getListPropuestaEconomica(this.props.data[this.state.activeStep + 1]);
  };
  handleBack = () => {
    this.setState(prevState => ({
      activeStep: prevState.activeStep - 1,
    }));
    this.getListPropuestaEconomica(this.props.data[this.state.activeStep - 1]);
  };

  async getListPropuestaEconomica(propuesta) {
    store.dispatch({
      type: 'SET_propuesta',
      payload: {
        propuesta: propuesta
      }
    })
    try {
      let res = await axios({
        ///propuestaCliente/idProposal/idCompra
        url: env.endpoint + '/propuestaCliente/' + propuesta.id ,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {

        this.setState({
          propuestaeconomica: res.data
        })
      } else if (res.status === 202) {

      }
      // return res.data
    }
    catch (error) {
    }
  }


  
  modificarInicio = () => {
    this.componentDidMount();
    this.props.inicio();
  }

  render() {
    const propuestaSteps = this.props.data;
    const { classes, theme } = this.props;
    const { activeStep } = this.state;
    const maxSteps = propuestaSteps.length;

    return (this.props.viewEstate?
     
     
     <div className={classes.root}>
        <Paper square elevation={0} className={classes.header}> {propuestaSteps[activeStep].nombre} - {propuestaSteps[activeStep].nit}  # {propuestaSteps[activeStep].id}
        </Paper>

        <ShowPropuesta
          inicio={this.modificarInicio}
          compra={this.props.compra}
          propuesta={propuestaSteps[activeStep]}
          propuestaeconomica={this.state.propuestaeconomica}
        />

        <MobileStepper
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          className={classes.mobileStepper}
          nextButton={
            <Button size="small" onClick={this.handleNext} disabled={activeStep === maxSteps - 1}>
              Siguiente
              {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </Button>
          }
          backButton={
            <Button size="small" onClick={this.handleBack} disabled={activeStep === 0}>
              {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
              Anterior
            </Button>
          }
        />
      </div> :
      <div>
        <p>No tenemos ninguna propuesta ninguna propuesta registrada. </p>
      </div>
    );
  }
}

PagePropuestacontainer.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect(mapSteToProps)(withStyles(styles, { withTheme: true })(PagePropuestacontainer));