import React, { Component } from 'react';
import ErrorMessage from './ErrorMessage';
import 'bootstrap/dist/css/bootstrap.css';
import config from './Config';
import { UserAgentApplication , tokenReceivedCallBack , logger } from 'msal';
import { getUserDetails } from './GraphService';
import Button from '@material-ui/core/Button';
import { store } from '../../../store/store'
import axios from "axios";
import env from '../../config/env';




class App365 extends Component {
  constructor(props) {
    super(props);
    this.userAgentApplication = new UserAgentApplication(config.appId, null, tokenReceivedCallBack, { logger: logger, cacheLocation: 'localStorage'});
    var user = this.userAgentApplication.getUser();
    this.state = {
      isAuthenticated: (user !== null),
      user: {},
      error: null
    };
  
    if (user) {
      // Enhance user object with data from Graph
      this.getUserProfile();
    }
  }

  async login() {
    try {
      await this.userAgentApplication.loginPopup(config.scopes);
      await this.getUserProfile();

    }
    catch(err) {
      var errParts = err.split('|');
      this.setState({
        isAuthenticated: false,
        user: {},
        error: { message: errParts[1], debug: errParts[0] }
      });
    }
  }


  logout() {
    this.userAgentApplication.logout();
  }

  async getUserProfile() {
    try {
      // Get the access token silently
      // If the cache contains a non-expired token, this function
      // will just return the cached token. Otherwise, it will
      // make a request to the Azure OAuth endpoint to get a token
  
      var accessToken = await this.userAgentApplication.acquireTokenSilent(config.scopes);
  
      if (accessToken) {
        // Get the user's profile from Graph
        var user = await getUserDetails(accessToken);

        this.comprobarIdentidad(accessToken);
        this.setState({
          isAuthenticated: true,
          user: {
            displayName: user.displayName,
            email: user.mail || user.userPrincipalName
          },
          error: null
        });
      }
    }
    catch(err) {
      var error = {};
      if (typeof(err) === 'string') {
        var errParts = err.split('|');
        error = errParts.length > 1 ?
          { message: errParts[1], debug: errParts[0] } :
          { message: err };
      } else {
        error = {
          message: err.message,
          debug: JSON.stringify(err)
        };
      }
  
      this.setState({
        isAuthenticated: false,
        user: {},
        error: error
      });
    }
  }
  async comprobarIdentidad(accessToken){
   
    try {
      let res = await axios({
      method: 'get',
      url: env.endpoint + '/auth_365',
      headers: {
        'Accept': '*/*',
        'Authorization': accessToken,
      }
    })

    if (res.status === 200) {
   //   console.log(res.data.auth_token)
      store.dispatch({
        type: 'SET_USER_ACCESO',
        payload: {
         userAcceso: res.data.auth_token
        }
      })
   this.props.inicio();

    }else if (res.status === 203){

      this.setState({
      error :{
        message: res.data.Message + 'en 5 segundos se cerrara sesion con 365.',
        debug: 'User no autorizado'
      }
    })
      setTimeout(()=>this.logout(), 5000)
    }
  } catch (error) {
   console.log(error);
  }
}

  render() {
    let error = null;
    if (this.state.error) {
      error = <ErrorMessage message={this.state.error.message} debug={this.state.error.debug} />;
    }
    return (
        <div >
    

            <p>Iniciar secci√≥n con 365 </p>
            {this.state.isAuthenticated? <div>Estas autenticado con   {this.state.user.displayName + ' ' + this.state.user.email } 
            <Button  variant="contained" type="submit" color="primary"  onClick={()=>this.logout()}> cerrar sesi√≥n</Button> </div>: 
            
            <Button variant="contained" type="submit" color="primary" onClick={()=>this.login()}> Iniciar secci√≥n con 365</Button> }
         
            {error} 
           
        </div>
    );
  }
  setErrorMessage(message, debug) {
    this.setState({
      error: {message: message, debug: debug}
    });
  }
}

export default App365;