import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Icon from '@material-ui/core/Icon';
import Pagination from "react-js-pagination"; 
import ListDetalleComprometido from '../comprometido/listDetalleComprometido';
import ListDetalleGasto from '../gasto/listDetalleGasto';
import env from "../config/env";
import axios from "axios";
import { connect } from 'react-redux';

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
  } 


const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};

export class ListPresupuesto extends React.Component {

    state = {
        open: false,
        openG:false, 
        row : {},
        datos:{data:{}}, 
        gatosRow:{},
        gatosDatos:{data:{}}
      };
    
      async getData(row , page) {
        try {
          let res = await axios({
            url: env.endpoint + '/PresupuestoDetalle/llave/?cluster_id='+ row.cluster_id+'&componente_id='+ row.componente_id+'&operacion_id=' + row.operacion_id+'&proyecto_id='+ row.proyecto_id+'&periodo_id=1&page='+ page,
            method: 'get',
            //   //  timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if (res.status === 200) {
            // peticion correctamente
            this.setState({ datos: res.data });
    
          }
        }
        catch (error) {
       //   this.getError(error.response);
        }
      }


      async getGatosData(row , page) {
        try {
          let res = await axios({
            url: env.endpoint + '/presupuestoComprometido/llave/?cluster_id='+ row.cluster_id+'&componente_id='+ row.componente_id+'&operacion_id=' + row.operacion_id+'&proyecto_id='+ row.proyecto_id+'&periodo_id=1&page='+ page,
            method: 'get',
            //   //  timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if (res.status === 200) {
            // peticion correctamente
            this.setState({ gatosDatos: res.data });
    
          }
        }
        catch (error) {
       //   this.getError(error.response);
        }
      }

    
        handlePageChangeCompro = (pageNumber) => {
          
            this.setState({ activePagecompro: pageNumber });
              this.getData(this.state.row,pageNumber );
          }

          handlePageChangeGasto = (pageNumber) => {
          
            this.setState({ activePagegasto: pageNumber });
              this.getGatosData(this.state.row,pageNumber );
          }
  
          
      handleClickOpen = ( row) => {
        this.setState({ open: true , row : row});
       this.getData(row, 1);
      };
      handleClickOpenGasto = ( row) => {
        this.setState({ openG: true , gatosRow : row});
       this.getGatosData(row, 1);
      };
    
      handleClose = () => {
        this.setState({ open: false });
      };
      handleCloseG = () => {
        this.setState({ openG: false });
      };

    modificar(id) {
        this.props.modificar(id);
    }

    eliminar(id) {
        this.props.onEliminar(id);
    }
    getMapData() {
        if (this.props.datos) {
            return this.props.datos.data.map(row => {
                return (
                    <TableRow key={row.id}>
                     <TableCell >{row.id}</TableCell>
                      <TableCell >{row.clusterName}</TableCell>
                        <TableCell >{row.componenteName}</TableCell>
                        <TableCell >{row.proyectoName}</TableCell>
                        <TableCell >{row.operationName}</TableCell>
                        <TableCell >{
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.totalPresupuesto)
                        }</TableCell>
                        <TableCell >
                        <p onClick={() =>this.handleClickOpenGasto(row)}> 
                        {
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.TotalGasto)
                        }
                        </p>
                        </TableCell>


                        <TableCell >
                       <p onClick={() =>this.handleClickOpen(row)}> {
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.comprometido)
                        }</p>
                    </TableCell>
                   <TableCell >
                        {
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.disponible)
                        }
            </TableCell> 
            {this.props.action ? (<TableCell > <Icon color="primary" onClick={() => this.modificar(row.id)} >edit_icon</Icon>
                </TableCell>) : <TableCell ></TableCell>}
                    </TableRow>
                );
            });
        }
        return "";
    }
    setData() {
        return (
            <div style={styles.tableWrapper}>
                <Table>
                    <TableHead>
                        <TableRow>
                        <TableCell ><Icon color="primary" onClick={ () =>  this.props.cleardata()} >brush</Icon> #Llave</TableCell>
                            <TableCell > 
                        {this.props.clusterA?<Icon color="primary" onClick={ () =>  this.props.handleClickOpen('cluster')} >visibility</Icon>:''} Cluster 
                          </TableCell>
                            <TableCell> 
                                {this.props.componenteA?  <Icon color="primary" onClick={ () =>  this.props.handleClickOpen('componente')} >visibility</Icon>: ''} Componente</TableCell>
                            <TableCell >Concepto</TableCell>
                            <TableCell >
                                {this.props.operationn?  <Icon color="primary" onClick={ () =>  this.props.handleClickOpen('operacion')} >visibility</Icon>:''}
                                 Centro de Operaci√≥n
                                 
                            </TableCell>
                            <TableCell >$ Presupuesto</TableCell>
                            <TableCell >$ Gasto</TableCell>
                            <TableCell >$ Comprometido</TableCell>
                            <TableCell >$ Disponible </TableCell>
                            {this.props.action ? <TableCell >Acciones</TableCell> : <TableCell ></TableCell>}
                        </TableRow>
                    </TableHead>
                    { this.props.datos.data.length > 0?
                    <TableBody>
                        {this.getMapData()}
                        <TableRow >
                            <TableCell rowSpan={3} />
                            <TableCell colSpan={4}>Total</TableCell>
                            <TableCell align="right">
                        {
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(this.props.datos.totalPresupuesto)
                        }
                          </TableCell>
                          <TableCell align="right">
                        {
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(this.props.datos.TotalGasto)
                        }
                          </TableCell>
                          <TableCell align="right">
                        {
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(this.props.datos.TotalComprometido)
                        }
                          </TableCell>
                          <TableCell align="right">
                        {
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(this.props.datos.TotalDisponible)
                        }
                          </TableCell>
                        </TableRow>
                    </TableBody>
                    :(
                      <TableBody></TableBody>
                    )}
                </Table>
                {this.props.datos.data.length > 0? 
         <Pagination
          activePage={this.props.activePage}
          itemsCountPerPage={10}
          totalItemsCount={this.props.datos.totalData}
          pageRangeDisplayed={5}
          onChange={this.props.handlePageChange}
        />: '' }

<ListDetalleComprometido open={this.state.open} handleClose ={this.handleClose} 
row={this.state.row}
datos={this.state.datos}
activePage={this.state.activePagecompro}
handlePageChange={this.handlePageChangeCompro} />

<ListDetalleGasto open={this.state.openG} handleClose ={this.handleCloseG} 
row={this.state.gatosRow}
datos={this.state.gatosDatos}
activePage={this.state.activePagegasto}
handlePageChangeGastos={this.handlePageChangeGasto} />
        </div>
        );
    }
    render() {
        return this.setData();
    }
}

export default connect(mapSteToProps)(ListPresupuesto);