import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';
import { connect } from 'react-redux';
import Grid from '@material-ui/core/Grid';

const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso , compra: state.compra }
}

export class ListPresupuestoafectado extends React.Component {
 
    getMapData() {
        if (this.props.datos) {
            return this.props.datos.data.map(row => {
                return (
                    <TableRow key={row.id}>
                        <TableCell >{row.servicio}</TableCell>
                        <TableCell >{row.componenteName}</TableCell>
                        <TableCell >{row.clusterName}</TableCell>
                        <TableCell >{row.proyectoName}</TableCell>
                        <TableCell >{row.operacionName}</TableCell>
                        <TableCell >{row.cantidad}</TableCell>
                        <TableCell  > 
                            <Tooltip disableFocusListener disableTouchListener title={row.porcentaje}>
                       <p> {
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.valorPresupuesto)
                            }</p>
                            </Tooltip>
                        </TableCell>

                        <TableCell >{
                            new Intl.NumberFormat('COP', {
                                style: 'currency',
                                currency: 'COP',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.totalPago)
                        }</TableCell>
                    </TableRow>
                );
            });
        }
 return "";
    }
    setData() {
        return (<Paper >
            <div style={styles.tableWrapper}>
                <Table  >
                    <TableHead>
                        <TableRow>
                            <TableCell>Servicio</TableCell>
                            <TableCell >Componente</TableCell>
                            <TableCell >Cl√∫ster</TableCell>
                            <TableCell >Concepto</TableCell>
                            <TableCell >Centro de operaci√≥n</TableCell>
                            <TableCell >Cantidad</TableCell>
                            <TableCell >$Valor presupuesto</TableCell>
                            <TableCell >$Gasto</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                        <TableRow >
                            <TableCell rowSpan={3} />
                            <TableCell colSpan={5}>Totales</TableCell>
                            <TableCell align="right">{
                                new Intl.NumberFormat('COP', {
                                    style: 'currency',
                                    currency: 'COP',
                                    minimumFractionDigits: 0,
                                    maximumFractionDigits: 0
                                }).format(this.props.datos.TotalPresupuestado)}</TableCell>
                        
                        <TableCell align="right">{
                                new Intl.NumberFormat('COP', {
                                    style: 'currency',
                                    currency: 'COP',
                                    minimumFractionDigits: 0,
                                    maximumFractionDigits: 0
                                }).format(this.props.datos.TotalCompra)}</TableCell>
                        </TableRow>
                        <TableRow >
                            <TableCell rowSpan={4} />
                            <TableCell colSpan={4}>Ahorro</TableCell>
                            <TableCell align="right"></TableCell>
                        <TableCell align="right">{
                                new Intl.NumberFormat('COP', {
                                    style: 'currency',
                                    currency: 'COP',
                                    minimumFractionDigits: 0,
                                    maximumFractionDigits: 0
                                }).format(this.props.datos.TotalPresupuestado - this.props.datos.TotalCompra)}</TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </div>
        </Paper>);
    }
    render() {
        return (  <div>

<Grid container spacing={24}>
          <Grid item xs={12} sm={6}>
            <p><b>Fecha de aprobaci√≥n: </b>{this.props.datos.infoAprobacion.created_at}

            </p>
            <p><b>Nombre responsable:</b> {this.props.datos.infoAprobacion.name} {this.props.datos.infoAprobacion.apellido}
            </p>
            <p><b>Cargo:</b> {this.props.datos.infoAprobacion.cargo}
            </p>
            <p><b>Acci√≥n o forma de aprobaci√≥n:</b> {this.props.datos.infoAprobacion.action}
            </p>
          </Grid>
          <Grid item xs={12} sm={6}>
            <p><b>comentario de aprobaci√≥n</b> {this.props.datos.infoAprobacion.descripcionAprobacion}
            </p>
            <p><b>ID - propuesta:</b>  {this.props.datos.infoAprobacion.idProposal}
            </p>
            <p><b>Comentario propuesta:</b> {this.props.datos.infoAprobacion.description}
            </p>
          </Grid>
        </Grid>

{this.setData()}
        </div>)
    }
}

  
  export default  connect(mapSteToProps)(ListPresupuestoafectado);