import React from 'react';
import ListRules from './list-rules';
import { connect } from 'react-redux';
import { store } from '../../store/store';
import env from "../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import PropTypes from 'prop-types';
import NumberFormat from 'react-number-format';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}
function NumberFormatCustom(props) {
  const { inputRef, onChange, ...other } = props;

  return (
      <NumberFormat
          {...other}
          getInputRef={inputRef}
          onValueChange={values => {
              onChange({
                  target: {
                      value: values.value,
                  },
              });
          }}
          thousandSeparator
          prefix="$"
      />
  );
}
NumberFormatCustom.propTypes = {
  inputRef: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
};

class FormRules extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      datos: { data: [] },
      error: {},
      id: '',
      name: '',
      valueMini: '',
      valueMax: '', 
      canSubmit: true,
      action: true,
    }
  }

  componentDidMount() {
    this.getData(1);
  }


  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }

  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }

  async getData(page) {
    try {
      let res = await axios({
        url: env.endpoint + '/rulepurchases?page=' + page,
        method: 'get',
       // timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ datos: res.data });

      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  modificar = (id) => {
    this.getperiodo(id)
  }

  listData() {
    if (this.state.datos.data.length !== 0) {
      return <ListRules handlePageChange={this.handlePageChange} datos={this.state.datos} modificar={this.modificar} activePage={this.state.activePage} action={true} />;
    } else {
      return "";
    }
  }

  
  handlePageChange = (pageNumber) => {
    this.setState({ activePage: pageNumber });
    this.getData(pageNumber);
  }



  updateStado = () => {
    this.setState({
      activo: !this.state.activo,
    });
  }

  modificar = (id) => {
    this.getcluster(id)
  }
  async getcluster(id) { // Obtener los datos para modificar 
    try {
      let res = await axios({
        url: env.endpoint + '/rulepurchases/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({
          action: false,
          id: res.data.id,
          name: res.data.name,
          valueMini: res.data.valueMini,
          valueMax: res.data.valueMax

        })
      }
    } catch (error) {
      this.getError(error.response);
    }
  }



  onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/rulepurchases',
      data: {
        name: this.state.name,
        valueMini: this.state.valueMini,
        valueMax: this.state.valueMax
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      if (response.status === 203) {
        // peticion correctamente
        this.setState({
          Mensaje: true,
          error: { activo: response.data.Error },
          canSubmit: true
        })

      } else {
        this.clear();
        this.getData(1);
        this.setState({ activePage: 1 });
        this.setState({ canSubmit: true });
      }
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });

  }
  onSubmitEditar = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'put',
      url: env.endpoint + '/rulepurchases/' + this.state.id,
      data: {
        id: this.state.id,
        name: this.state.name,
        valueMini: this.state.valueMini,
        valueMax: this.state.valueMax
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      if (response.status === 203) {
        // peticion correctamente
        this.setState({
          Mensaje: true,
          error: { activo: response.data.Error },
          canSubmit: true
        })

      } else {
        this.clear();
        this.getData(1);
        this.setState({ activePage: 1 });
        this.setState({ canSubmit: true });
      }
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }


  clear() {
    this.setState({
      action: true,
      id: '',
      name: '',
      valueMini: '',
      valueMax: '', 
      error: {},
    })
  }
  render() {
    return (
      <div>
        <h3>Admin  grupo de reglas </h3>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6}>
            <TextField
              error={this.state.error.name ? true : false}
              label={this.state.error.name ? this.state.error.name[0] : "Nombre"}
              placeholder="Nombre "
              value={this.state.name}
              onChange={(e) => this.syncField(e, 'name')}
              fullWidth={true}
            />
          </Grid>
          
          <Grid item xs={12} sm={6}>
            <TextField
              error={this.state.error.valueMini ? true : false}
              label={this.state.error.valueMini ? this.state.error.valueMini[0] : "Valor M√≠nimo"}
              placeholder="Valor M√≠nimo "
              value={this.state.valueMini}
              onChange={(e) => this.syncField(e, 'valueMini')}
              fullWidth={true}
              InputProps={{
                inputComponent: NumberFormatCustom,
            }}
            />
            <TextField
              error={this.state.error.valueMax ? true : false}
              label={this.state.error.valueMax ? this.state.error.valueMax[0] : "Valor M√°ximo"}
              placeholder="Valor m√°ximo "
              value={this.state.valueMax}
              onChange={(e) => this.syncField(e, 'valueMax')}
              fullWidth={true}
              InputProps={{
                inputComponent: NumberFormatCustom,
            }}
            />  
          </Grid>
        </Grid>
        
        {this.state.action ?
          <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
          <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
        }
        {this.listData()
        }
      </div>
    );

  }
}

export default connect(mapSteToProps)(FormRules);