import React from 'react';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
//importamos las tablas 
import LisDetallePresupuesto from '../detallePresupuesto/lisdetallepresupuesto';
import TerminosReferencia from '../terminosReferencia';
import Entregables from '../entregables';

import ListPresupuestoafectado from '../../../presupuesto/list-presupuesto-afectado';
function mapSteToProps(state) {

  return {
    userAcceso: state.userAcceso
  }
}

class DetallesCompra extends React.Component {

 


  lispresupuestoAfect() {
    if (this.props.presupuestoAfect.data.length !== 0) {
      return <ListPresupuestoafectado datos={this.props.presupuestoAfect} />;
    } else {
      return "Error no tenemos presupuesto no se puede continuar...";
    }
  }

  listData() {
    if (this.props.presupuesto.data.length !== 0) {
      return <LisDetallePresupuesto datos={this.props.presupuesto} />;
    } else {
      return "Error no tenemos presupuesto no se puede continuar...";
    }
  }
  render() {
    return (
      <div>
        <Card className="HeartTitle">
          <p>1. Datos Solicitante</p>
        </Card>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6}>
            <p><b>Fecha de solicitud:   </b>
              {this.props.fechaSolicitud}
            </p>
            <p><b>Solicitante :</b> {this.props.user.name}   {this.props.user.apellido}
            </p>
            <p><b>Cargo:</b> {this.props.user.cargo}
            </p>
            <p><b>Direcci√≥n:</b> {this.props.user.direccion}
            </p>
          </Grid>
          <Grid item xs={12} sm={6}>
            <p><b>Identificaci√≥n:</b> {this.props.user.documento}
            </p>
            <p><b>E-mail:</b> {this.props.user.email}
            </p>
          </Grid>
        </Grid>
        <Card className="HeartTitle">
          <p>2. Datos generales de compra</p>
        </Card>

        <Grid container spacing={24}>
          <Grid item xs={12} sm={12}>
          <p><b>N¬™ compra</b> {this.props.COMPRADATA.id}
            </p>
            <p><b>Bien o servicio a contratar:</b> {this.props.COMPRADATA.NombreCompra}
            </p>
            <p><b>Objeto:</b> {this.props.COMPRADATA.objetivo}
            </p>
          </Grid>
          <Grid item xs={12} sm={6}>
            <p><b>Fecha de inicio:</b> {this.props.FechaInicio}
            </p>
            <p><b>Fecha de fin:</b> {this.props.FechaFin}
            </p>
          </Grid>
          <Grid item xs={12} sm={6}>
            <p><b>Plazo de ejecuci√≥n:</b> {this.props.FechaInicio - this.props.FechaFin}
            </p>
            <p><b>D√≠as para el inicio:</b> {this.props.COMPRADATA.FechaInicio}
            </p>
          </Grid>
        </Grid>


        {this.props.COMPRADATA.EstadoAction === 3 ? (
          <div> <Card className="HeartTitle" ><p>3. Presupuesto afectado.</p> </Card>
            {this.lispresupuestoAfect()} <br></br>
          </div>
        ) : (<div>
          <Card className="HeartTitle" ><p>3. Presupuesto Disponible.</p> </Card>
          {this.listData()} <br></br>
        </div>
          )}
        <Card className="HeartTitle">
          <p>4. T√©rminos  de referencia</p>
        </Card>
        <TerminosReferencia datos={this.props.TerminosReferencia} action={false} />

        <Card className="HeartTitle">
          <p>5. Entregables</p>
        </Card>
        <Entregables datos={this.props.entregables} />
      </div>
    )
  }
}
export default connect(mapSteToProps)(DetallesCompra);