import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableViews from 'react-swipeable-views';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import PagePropuestaForm from './page-propuesta-form';
import PagePropuestacontainer from '../components/propuesta/page-propuesta-componente';
import DetallesCompra from '../../compra/componentes/detalles/detallesCompra';
import CircularProgress from '@material-ui/core/CircularProgress';
import env from "../../config/env";
import axios from "axios";
import Grid from '@material-ui/core/Grid';
import jwtDecode from "jwt-decode";

import Acciones from '../../compra/componentes/acciones';
import { connect } from 'react-redux';

import { store } from '../../../store/store';

const Styles = ({
  cont: {
    display: 'flex',
    justifyContent: 'center'
  },
});

function mapSteToProps(state) {
  return { User: state.UserData, userAcceso: state.userAcceso }
}


function TabContainer({ children }) {
  return (
    <div style={{ padding: 8 * 3 }}>
      {children}
    </div>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: 500,
  },
});

class PagePropuesta extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      compra: {},
      user: {},
      value: 0,
      presupuestoAfect: { data: [] },
      entregables: [],
      TerminosReferencia: [],
      presupuesto: { data: [] },
      fechaSolicitud: '',
      FechaInicio: '',
      FechaFin: '',
      viewEstate: false,
      data: {},
      estadocompra: 0, 
      MessageErrorAccion: '',
      ErrorAccion: false, 
      open: false,
      canSubmit: true,
      propuestas: false,
    }
  };

  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }
  componentDidMount() {
    if (this.props.match.isExact) {
      this.getData(this.props.match.params.idCompra);
      if (this.props.location.search === '?R') {
        this.setState({ value: 2 });

      }

      var dt = new Date(this.state.compra.created_at).toLocaleDateString();
      this.setState({
        fechaSolicitud: dt,
        FechaInicio: new Date(this.state.compra.FechaInicio).toLocaleDateString(),
        FechaFin: new Date(this.state.compra.FechaFin).toLocaleDateString(),
      })
    } else {
      this.props.history.push('/compras?Error404')
    }
  }


  componentDidUpdate(prevProps) {
    if (prevProps.match.params.idCompra !== this.props.match.params.idCompra) {
      this.getData(this.props.match.params.idCompra);
    }
  }


  modificarInicio = () => {
    this.getData(this.props.match.params.idCompra);
  }



  async getPresupuestoAfectado(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listdetallePresupuesto/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ presupuestoAfect: res.data });

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }



  async getData(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/compras/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      // peticion correctamente
      //  console.log(res.data);
      if (res.status === 200) {
        this.setState({
          compra: res.data,
          data:{}
        })
        if (res.data.EstadoAction === 3) {
          this.getPresupuestoAfectado(res.data.id);
        }
        
        //llamamos lo metodos que hay 
        this.getUser(res.data.user_id);
        this.getentregables(res.data.id);
        this.getTerminos(res.data.id);
        this.getPresupuesto(res.data.id);
        this.getPropuesta(res.data.id);
        var dt = new Date(this.state.compra.created_at).toLocaleDateString();
        this.setState({
          fechaSolicitud: dt,
          FechaInicio: new Date(this.state.compra.FechaInicio).toLocaleDateString(),
          FechaFin: new Date(this.state.compra.FechaFin).toLocaleDateString(),
          estadocompra: res.data.EstadoAction
        })
      } else {
        this.props.history.push('/compras?Error404')
      }
    }
    catch (error) {
      this.props.history.push('/compras?Error404')
    }
  }




  async getUser(id) {
    try {
      let res = await axios({
        ///users/iduser
        url: env.endpoint + '/users/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        if (res.data.length !== 0) {
          this.setState({
            user: res.data,

          })
        }
      } else if (res.status === 202) {

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  async getentregables(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listEntregables/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({ entregables: res.data });
      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  async getTerminos(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listreferenceterms/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({ TerminosReferencia: res.data });

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      //  this.getError(error.response);
    }
  }


  async getPresupuesto(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/ListdetallePresupuesto/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ presupuesto: res.data });

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }


  //showpropuestas/idCompra
  async getPropuesta(id) {
    try {
      let res = await axios({
        ///propuestaCliente/idProposal
        url: env.endpoint + '/showpropuestas/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente

        if (res.data.length !== 0) {
          this.setState({
            viewEstate: true,
            data: res.data,
            propuestas: true
          })

          // this.getListPropuestaEconomica(this.state.data[this.state.activeStep]);

        } else {
          this.setState({
            viewEstate: false,
             propuestas: false
          })
        }

      } else if (res.status === 202) {
        this.setState({
          viewEstate: false,
          propuestas: false
        })
      }
    }
    catch (error) {
      console.log(error);
    }
  }

  handleChange = (event, value) => {
    this.setState({ value });
  };
  handleChangeIndex = index => {
    this.setState({ value: index });
  };

  handleChangeaction = event => {
  this.setState({ estadocompra: parseInt(event.target.value) });
    };

  updateEstadocompra=()=>{
      this.updateEstado();
    }

    async updateEstado() {
      try {
        let res = await axios({
          url: env.endpoint + '/updateEstado/' + this.state.compra.id + '?Estado='+ this.state.estadocompra,
          method: 'get',
          timeout: 8000,
          headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
        })
        if (res.status === 200) {
          // peticion correctamente
          this.getData(this.props.match.params.idCompra);
          this.setState({
            MessageErrorAccion:'', 
            ErrorAccion: false,
            open: false,  
            value: 0
           })
  
          }else if(res.status === 203 ){
           this.setState({
            MessageErrorAccion: res.data.message , 
            ErrorAccion: true
           })
          }
      }
      catch (error) {
        if(error.response.status === 403 ){
          this.setState({
           MessageErrorAccion: error.response.data[0] , 
           ErrorAccion: true
          })
         }
      }
    }


    handleClickOpen = () => {
      this.setState({ open: true });
      
    };
    handleClose = () => {
      this.setState({ open: false });
    };

  getEstado(estado) {
    switch (estado) {
      case 0:
        return 'Pendiente por Confirmar - Edici√≤n '
      case 1:
        return 'Compra Anulada'
      case 2:
        return 'Compra habilitada para recibir propuestas.'
      case 3:
        return 'Compra Aprobada'
      case 4:
        return 'Compra en Aprobaci√≤n. '
      default:
        return ''
    }
  }
  render() {
    const { theme } = this.props;
    return (
      <div >
        <Paper >
          <Tabs
            value={this.state.value}
            onChange={this.handleChange}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            <Tab label="Compra" />
            <Tab label="Propuestas" />

            {this.state.compra.EstadoAction === 2 ?
              <Tab label="Registrar" /> :
              ''}
          </Tabs>
        </Paper>



        <Grid container spacing={24}>
          <Grid item xs={12} sm={9}>
            {this.getEstado(this.state.compra.EstadoAction)}
          </Grid>
          <Grid item xs={3} sm={3}>
          {   jwtDecode(this.props.userAcceso).rol=== 'Super-Admin' || jwtDecode(this.props.userAcceso).user_id === this.state.compra.user_id ?
           <Acciones handleChangeaction={this.handleChangeaction}
           estado={this.state.estadocompra}
           MessageErrorAccion={this.state.MessageErrorAccion}
           ErrorAccion={this.state.ErrorAccion}
           updateEstadocompra={this.updateEstadocompra}
           handleClickOpen={this.handleClickOpen}
           handleClose={this.handleClose}
           open={this.state.open}
           canSubmit={this.state.canSubmit}
           /> :'' }
      
        </Grid>
        </Grid>




        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={this.state.value}
          onChangeIndex={this.handleChangeIndex}
        >


          <TabContainer >
            {this.state.compra.id > 0 ?
              <DetallesCompra
                user={this.state.user}
                entregables={this.state.entregables}
                TerminosReferencia={this.state.TerminosReferencia}
                presupuesto={this.state.presupuesto}
                presupuestoAfect={this.state.presupuestoAfect}
                fechaSolicitud={this.state.fechaSolicitud}
                FechaInicio={this.state.FechaInicio}
                FechaFin={this.state.FechaFin}
                COMPRADATA={this.state.compra} /> :
              <div style={Styles.cont} > 
                <CircularProgress color="secondary" /> 
                </div>
            }
          </TabContainer>
          <TabContainer >
            {this.state.compra.id > 0  && this.state.data.length > 0?

              <PagePropuestacontainer inicio={this.modificarInicio}
                compra={this.state.compra}
                viewEstate={this.state.viewEstate}
                data={this.state.data}
              /> :
              this.state.compra.propuestas ? <div style={Styles.cont} >   <CircularProgress color="secondary" /> </div>:''
            }

          </TabContainer>
          {this.state.compra.EstadoAction === 2 ?
            <TabContainer >
              < PagePropuestaForm
                compra={this.state.compra}

              />
            </TabContainer> : ''}
        </SwipeableViews>
      </div>
    );
  }
}
PagePropuesta.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect(mapSteToProps)(withStyles(styles, { withTheme: true })(PagePropuesta));