import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import FormTerminosReferenciaProvedor from './form-terminos-referencia';
import FormEntregable from './form-entregable';
import FormPropuesta from '../propuesta/form-propuesta';
import ListConfictos from '../../conflicto-interes/list-confictos';
import Icon from '@material-ui/core/Icon';

const styles = theme => ({
    root: {
        width: '90%',
    },
    backButton: {
        marginRight: theme.spacing.unit,
    },
    instructions: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
    }, cont: {
        display: 'flex',
        justifyContent: 'center'
    },
});

function getSteps() {
    return ['Crear Propuesta.', ' Conflictos de Inter√©s', 'T√©rminos  de referencia', 'Productos o entregables'];
}
class PagePoliticas extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            activeStep: 0,
            propuesta: {},
            siguiente: true,
        }
    }


    componentDidMount() {

        this.props.siguienteUpdate(false);

    }
    action() {
        if (this.props.compra.id > 0) {

            return (
                <div>
                    <p>Por favor indique si el proveedor que se presenta cumple con los t√©rminos establecidos. <br></br>No olvides guardar tus respuestas dando clic a cada <Icon variant="contained" type="submit"  >done_all</Icon> </p>
                    <FormTerminosReferenciaProvedor form={true} provedor={this.props.provedor} idCompra={this.props.compra.id} />
                </div>
            );
        } else {
            return 'Estamos comprobando..';
        }
    }
    entregables() {
        if (this.props.compra.id > 0) {
            return (
                <div>
                    <p>Por favor indique si el proveedor que se presenta cumple con los productos o entregables establecidos. <br></br>No olvides guardar tus respuestas dando clic a cada <Icon variant="contained" type="submit"  >done_all</Icon> </p>
                    <FormEntregable form={true} provedor={this.props.provedor} idCompra={this.props.compra.id} />
                </div>);
        } else {
            return 'Estamos comprobando..';
        }
    }
    
    getStepContent(stepIndex) {
        switch (stepIndex) {
            case 0:
                return (<FormPropuesta
                    updateSiguiente={this.updateSiguiente}
                    handleBack={this.props.handleBack}
                    handleNext={this.handleNext}
                    dataPropuesta={this.dataPropuesta}
                    compra={this.props.compra}
                    provedor={this.props.provedor}
                />);
            case 1:
                return (
                    <div>
                        <p>Por favor responda las siguientes preguntas y guarde sus respuestas dando clic en el <Icon variant="contained" type="submit"  >done_all</Icon> </p>
                        <ListConfictos form={true} propuesta={this.state.propuesta} />
                    </div>
                );
            case 2:
                return this.action();
            case 3:
                return this.entregables();
            default:
                return 'Uknown stepIndex';
        }
    }

    dataPropuesta = (data) => {
        this.setState({
            propuesta: data,
            siguiente: true,
        });
    };

    handleNext = () => {
        if (this.state.activeStep === 3) {// validamos por ahora para continuar a registrar propuestas
            this.props.handleNext();
            this.props.propuesta(this.state.propuesta);// esto esta mal pero funciona por el render del dom
        } else {

            this.setState(state => ({
                activeStep: state.activeStep + 1,
            }));
        }
    };

    handleBack = () => {
        if (this.state.activeStep === 0) {
            this.props.handleBack();
        } else {
            this.setState(state => ({
                activeStep: state.activeStep - 1,
            }));
        }

    };
    handleReset = () => {
        this.setState({
            activeStep: 0,
        });
    };

    updateSiguiente = (action) => {
        this.setState({
            siguiente: action,
        });
    };


    render() {
        const { classes } = this.props;
        const steps = getSteps();
        const { activeStep } = this.state;
        return (
            <div >
                <Stepper activeStep={activeStep} alternativeLabel>
                    {steps.map(label => {
                        return (
                            <Step key={label}>
                                <StepLabel>{label}</StepLabel>
                            </Step>
                        );
                    })}
                </Stepper>
                <div>
                    {this.state.activeStep === steps.length ? (
                        <div>
                            <Typography className={classes.instructions}>All steps completed</Typography>
                            <Button onClick={this.handleReset}>Reset</Button>
                        </div>
                    ) : (
                            <div>

                                {this.getStepContent(activeStep)}

                                {this.state.siguiente ?
                                    <div className={classes.cont}>
                                        <Button
                                            onClick={this.handleBack}
                                            className={classes.backButton}
                                        >
                                            anterior
                                    </Button>
                                        <Button variant="contained" color="primary" onClick={this.handleNext}>
                                            {activeStep === steps.length - 1 ? 'Confirmar' : 'Siguiente'}
                                        </Button>
                                    </div>

                                    : ''}
                            </div>
                        )}
                </div>
            </div>
        );
    }
}
PagePoliticas.propTypes = {
    classes: PropTypes.object,
};
export default withStyles(styles)(PagePoliticas);