import React from 'react';
export default class ErrorMessage extends React.Component {
  render() {
    let debug = null;
    if (this.props.debug) {
      debug = <pre className="alert-pre border bg-light p-2"><code>{this.props.debug}</code></pre>;
    }
    return (
      <div>
        <p className="mb-3">{this.props.message}</p>
        {debug}
      </div>
    );
  }
}