
import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import { store } from '../../store/store';
import { connect } from 'react-redux';
import Drawer from '@material-ui/core/Drawer';
import MenuApp from './menuapp';
import LinkButton from './LinkButton';
import Button from '@material-ui/core/Button';
import jwtDecode from "jwt-decode";
import InputBase from '@material-ui/core/InputBase';
import { fade } from '@material-ui/core/styles/colorManipulator';
import Fade from '@material-ui/core/Fade';

import Search from './Search';
function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}

const styles = theme =>( {
  root: {
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  color:{
color: 'black'
  }, 
  btn:{
    color: '#fff',
}, 
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  list: {
    width: 250,
  },
  appBar: {
    top: 'auto',
  },
  fullList: {
    width: 'auto',
  },
  logo:{
    width: '9%',
    padding: '5px'
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing.unit,
      width: 'auto',
    },
  },
  
  inputRoot: {
    color: 'inherit',
    width: '100%',
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: '10px',
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 120,
      '&:focus': {
        width: 200,
      },
    },
  },
});

class MenuAppBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
      anchorEl2: null, 
      user:{} , 
      top: false,
      left: false,
      bottom: false,
      right: false,
      idcompra: 1
    }
  }
  toggleDrawer = (side, open) => () => {
    this.setState({
      [side]: open,
    });
  };
  componentDidMount() {
    this.validarstado();
  }
  validarstado = () => {
    if (this.props.userAcceso !== null && this.props.userAcceso !== '') {
      this.setState({ auth: true });
    }
  };

  logout = (e) => {
    this.handleClose();
    //limpiamos  en el storage token de acceso.
    localStorage.clear();
    store.dispatch({
      type: 'SET_USER_ACCESO',
      payload: {
        userAcceso: ''
      }
    })
     
  }

  handleChange = event => {
    this.setState({ auth: event.target.checked });
  };

  handleMenu = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  handleClick = event => {
    this.setState({ anchorEl2: event.currentTarget });
  };
  handleClose2 = () => {
    this.setState({ anchorEl2: null });
  };

  syncField(ev, fieldName) {
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }


 
  render() {
    const { classes } = this.props;
    const { anchorEl } = this.state;
    const open = Boolean(anchorEl);
    const { anchorEl2 } = this.state;
    const open2 = Boolean(anchorEl2);

    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
          
            {/*
  
             this.props.userAcceso && (<Button className={classes.menuButton} color="inherit" aria-label="Menu" onClick={this.toggleDrawer('left', true)}>
              <MenuIcon />
            </Button>) */}
            
            <Typography variant="h6" color="inherit" className={classes.grow}>
              Dividendo Compras
            </Typography>


            {!this.props.userAcceso && (
               <LinkButton to='/login' >Login</LinkButton>
            )}
            {this.props.userAcceso && (
              <div  >

              <LinkButton to='/Politicas' >Pol√≠ticas</LinkButton>
              <LinkButton to='/Presupuesto' >Presupuesto</LinkButton>
               {
                 //user Super-Admin
                  jwtDecode(this.props.userAcceso).rol=== 'Super-Admin'? 
                   <Button
                   className={classes.btn}
                      aria-owns={open ? 'fade-menu' : undefined}
                      aria-haspopup="true"
                      onClick={this.handleClick}
                    >
                      Administrador
                    </Button>
                    :''}
                    <Menu
                      id="fade-menu"
                      anchorEl={anchorEl2}
                      open={open2}
                      onClose={this.handleClose2}
                      TransitionComponent={Fade}
                    >
                <LinkButton   onClick={this.handleClose2} style={styles.color} to='/AdminUser' >Funcionarios</LinkButton>
                <LinkButton   onClick={this.handleClose2} style={styles.color} to='/AdminProvedores' >Proveedores</LinkButton>
                <LinkButton   onClick={this.handleClose2} style={styles.color} to='/clusters' >Clusters</LinkButton>
                <LinkButton   onClick={this.handleClose2} style={styles.color} to='/componentes' >Componentes </LinkButton> 
                <LinkButton   onClick={this.handleClose2} style={styles.color} to='/conceptos' >Conceptos</LinkButton>
                <LinkButton   onClick={this.handleClose2} style={styles.color} to='/rules' >Reglas</LinkButton>   
                </Menu>
                <LinkButton to='/compra' >Comprar</LinkButton>
                <LinkButton to='/compras' >Compras</LinkButton>
              </div>
              )}
             {this.props.userAcceso && (
             <div className={classes.search}>
              <InputBase
                placeholder="N¬™ compra"
                onChange={(e) => this.syncField(e, 'idcompra')}
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
              />
              
            </div>
            )}
                {this.props.userAcceso && (
            <Search Search={this.state.idcompra}  ></Search>
                )}
            {this.props.userAcceso && (
              <div>
                <IconButton
                  aria-owns={open ? 'menu-appbar' : undefined}
                  aria-haspopup="true"
                  onClick={this.handleMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={open}
                  onClose={this.handleClose}
                >
                  <MenuItem onClick={this.handleClose}>My account {jwtDecode(this.props.userAcceso).name}</MenuItem>
                  <MenuItem onClick={this.logout}>logout</MenuItem>
                </Menu>
              </div>
            )}
          </Toolbar>
        </AppBar>
        <Drawer open={this.state.left} onClose={this.toggleDrawer('left', false)}>
          <div
            tabIndex={0}
            role="button"
            onClick={this.toggleDrawer('left', false)}
            onKeyDown={this.toggleDrawer('left', false)}
          >
            <MenuApp className={classes.list} />
          </div>
        </Drawer>
      </div>
    );
  }
}

MenuAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(mapSteToProps)(withStyles(styles)(MenuAppBar));