import React from 'react';
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import { store } from '../../../store/store'
import TextField from '@material-ui/core/TextField';
import axios from "axios";
import env from '../../config/env';
import logo from '../../../assets/LOGODXC-01.jpg';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableViews from 'react-swipeable-views';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import App365 from '../365/App';


const styles = {
  root: {
    width: '98%',

  },
  cont: {
    display: 'flex',
    justifyContent: 'center',


  }, logo: {
    width: '20%',
    display: 'block',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
  main: {
    paddingLeft: '15%',
    paddingRight: '15%',
    paddingTop: '2%'

  }
};


function TabContainer({ children, dir }) {
  return (
    <Typography component="div" dir={dir}>
      {children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
  dir: PropTypes.string.isRequired,
};
function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: '',
      error: false,
      canSubmit: true,
      action: true,
      value: 0,
    };
  }

  componentDidMount() {
    if (this.props.userAcceso) {
      this.props.history.push('/home') //lo redirigimos si esta logueado jaja
    }
  }
  home() {
    this.props.history.push('/home')
  }

  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }


  onSubmit = () => {
    this.setState({
      error: false,
    })
    axios({
      method: 'post',
      url: env.endpoint + '/authenticate',
      data: {
        email: this.state.email,
        password: this.state.password,
      }, headers: {
        'Accept': '*/*',
      }

    }).then(response => {
      //registramos en el storage token de acceso.
      // es el unico nesesario por temas de seguridad para obtener la session de token deben decodificar el mismo
      store.dispatch({
        type: 'SET_USER_ACCESO',
        payload: {
          userAcceso: response.data.auth_token
        }
      })
      this.home();
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        if (error.response.status === 500) {
          this.setState({
            error: true,
          })
        }
      });
  }


  handleChange = (event, value) => {
    this.setState({ value });
  };

  handleChangeIndex = index => {
    this.setState({ value: index });
  };






  render() {

    const { theme } = this.props;
    return (
      <div >
        <Tabs
          value={this.state.value}
          onChange={this.handleChange}
          indicatorColor="primary"
          textColor="primary"
          centered
        >
          <Tab label="365" />
          <Tab label="login" />
        </Tabs>
        <img style={styles.logo} src={logo} alt="logo" />


        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={this.state.value}
          onChangeIndex={this.handleChangeIndex}
        >
          <TabContainer dir={theme.direction}>
            <div style={styles.main} >
              <App365 inicio={() => this.home()} />
            </div>
          </TabContainer>
          <TabContainer dir={theme.direction} >
            <div style={styles.main} >
              {this.state.error ? <p style={{ color: 'red' }}> Error Usuario o contrase√±a err√≥neos.</p> : ''}
              <TextField
                id="outlined-email-input"
                label="Email"
                type="email"
                name="email"
                autoComplete="email"
                onChange={(e) => this.syncField(e, 'email')}
                fullWidth={true}
                margin="normal"
                variant="outlined"
              />
              <TextField
                id="outlined-password-input"
                label="Password"
                type="password"
                autoComplete="current-password"
                onChange={(e) => this.syncField(e, 'password')}
                fullWidth={true}
                margin="normal"
                variant="outlined"
              />
              <Button variant="contained" type="submit" color="primary" onClick={this.onSubmit}> Login  </Button>
            </div>
          </TabContainer>
        </SwipeableViews>
      </div>
    );
  }
}

Login.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect(mapSteToProps)(withStyles(styles, { withTheme: true })(Login));
